// 1. Import necessary modules
import React from 'react';
// Import Swiper React components and modules you plan to use (e.g., Swiper, SwiperSlide)
// Swiper is usually imported from 'swiper/react' in a modern React/Next.js setup
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, Navigation, Pagination } from 'swiper/modules'; // Import any core modules you need

// 2. Import the local Material Effect module
// Adjust the path './lib/effect-material.esm.js' based on your actual file structure
import EffectMaterial from './lib/effect-material.esm.js';

// 3. Import CSS
// Import the necessary CSS files. Next.js handles CSS imports.
import 'swiper/css'; // Base Swiper styles
import './lib/effect-material.css'; // Material Effect styles
import './material.css'; // Your custom styles

// Optional: Import other Swiper styles if you use Navigation/Pagination
// import 'swiper/css/navigation';
// import 'swiper/css/pagination';

// 4. Define the component
export default function MaterialSlider() {
  const slides = [
    { id: 1, image: 'images/01.jpg', label: 'Slide 1' },
    { id: 2, image: 'images/02.jpg', label: 'Slide 2' },
    { id: 3, image: 'images/03.jpg', label: 'Slide 3' },
    { id: 4, image: 'images/04.jpg', label: 'Slide 4' },
  ];

  return (
    <div className="demo-slider">
      <Swiper
        // Pass the Material Effect module along with any others you use
        // Note: Swiper modules are imported from 'swiper/modules' not the main 'swiper' export
        modules={[Autoplay, Navigation, Pagination, EffectMaterial]}
        
        // Specify the effect
        effect="material"

        // Optional: Add the specific class for your custom CSS
        className="swiper-material" 
        
        // Optional: Example of material effect parameters
        materialEffect={{
          slideSplitRatio: 0.65,
        }}
        
        // Other Swiper parameters (optional)
        // centeredSlides={true}
        // slidesPerView={1.3}
        // spaceBetween={10} 
      >
        {slides.map((slide) => (
          <SwiperSlide key={slide.id}>
            {/* 5. Implement the required HTML Layout */}
            {/* Main requirement: wrap slide content with "swiper-material-wrapper" and "swiper-material-content" */}
            <div className="swiper-material-wrapper">
              <div className="swiper-material-content">
                {/* 6. Add elements with material effects attributes/classes */}
                <img
                  className="demo-material-image"
                  data-swiper-material-scale="1.25"
                  src={slide.image}
                  alt={slide.label}
                />
                <span className="demo-material-label swiper-material-animate-opacity">
                  {slide.label}
                </span>
              </div>
            </div>
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
}